.block_newsletter { 
  display: inline-block;
  float: left;
  width: 100%;
  overflow: hidden;
  position: relative;
  background: #9923ab;
  img {
    position: absolute;
    width: 100%;
    height: 500px;
    left: 0px;
    top: 0px;
    opacity: .5;
    object-fit: cover;
  }
  .form-newsletter {
    width: 100%;
    position: relative;
    padding: 40px 0px;
    .form-inputs {
      position: relative;
    }
    .newsletter-title {
      color: #fff;
      font-size: 30px;
      margin-bottom: 20px;
      text-align: center;
      font-weight: 700;
      line-height: 39px;
    }
  }

  .newsletter-form  {
    position: relative;
    width: 100%;
    max-width: 700px;
    margin: 0 auto;
    .newsletter-email {
      width: 100%;
      float: left;
      height: 50px;
      padding-left: 5px;
      margin-bottom: 5px;
      font-size: 13px;
    }
    .newsletter-button {
      width: 100%;
      font-size: 11px;
      font-weight: 600;
      height: 48px;
      padding: 0px;
      position: absolute;
      right: 1px;
      text-align: center;
      top: 1px;
      width: 30%;
      i {
        margin-right: 0px;
      }
    }
    p {
      color: #fff;
      font-size: 14px;
      line-height: normal;
      text-align: center;
      font-weight: 300;
    }
    .mesage {
      .alert {
        background-color: transparent;
        border-color: transparent;
        padding: 0px;
      }
    }
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .block_newsletter {
    .newsletter-form {
      p {
        font-size: 10px;
      }
    }
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .block_newsletter {
    width: 100%;
    margin-bottom: 25px;
    .form-newsletter {
      .newsletter-title {
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
        margin-top: 10px;
        border-bottom: 0px;
        font-size: 24px;
      }
    }
    .newsletter-form {
      width: 100%;
      .newsletter-email {
        font-size: 10px;
        width: 100%;
      }
      .newsletter-button {
        font-size: 10px !important;
      }
    }
  }
}
@media (min-width: 768px) and (max-width: 1000px) {
  .block_newsletter {
    width: 100% !important;
    text-align: center;
    .newsletter-title {
      width: 100%;
    }
    .newsletter-form {
      margin: 0 auto;
      width: 50%;
    }
  }
}



